import "./styles.css";
import { useState } from "react";

export default function App() {
  const [images, setImages] = useState([]);
  const [showImages, setShowImages] = useState(false);
  const [width, setWidth] = useState(80);
  return (
    <div className="App">
      <textarea
        rows="30"
        cols="50"
        onChange={(ev) => {
          const { data } = JSON.parse(ev.target.value);
          console.log(data);
          const urls = Object.values(data.brands)
            .map((entry) => {
              return [
                entry?.displayName,
                entry?.icon?.url,
                entry?.feature?.url,
                entry?.logo?.url
              ];
            })
            .reduce((acc, curr) => {
              return [...acc, [curr[0], curr[1], curr[2], curr[3]]];
            }, []);
          setImages(urls);
        }}
      ></textarea>
      <br />
      <input
        placeholder="image width"
        type="text"
        onChange={(ev) => setWidth(ev.target.value)}
      />
      <button
        onClick={() => {
          setShowImages(true);
        }}
      >
        Show images
      </button>
      {showImages && (
        <table>
          <thead>
            <td>Name</td>
            <td>Icon</td>
            <td>Feature</td>
            <td>Logo</td>
          </thead>
          {images.map((image) => (
            <tr>
              <td>{image[0]}</td>
              <td>
                <img src={image[1]} width={width} />
              </td>
              <td>
                <img src={image[2]} width={width} />
              </td>
              <td>
                <img src={image[3]} width={width} />
              </td>
            </tr>
          ))}
        </table>
      )}
    </div>
  );
}
